import React, { useState, useEffect, useCallback } from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Typography,
  Box,
  Alert,
  CircularProgress,
  IconButton,
  Tooltip,
} from '@mui/material';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { Refresh as RefreshIcon } from '@mui/icons-material';
import axios from 'axios';

const TrialBalance = () => {
  const [trialBalance, setTrialBalance] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [date, setDate] = useState(new Date());

  const fetchTrialBalance = useCallback(async () => {
    try {
      setLoading(true);
      setError(null);
      const response = await axios.get('/api/accounting/trial-balance', {
        params: { date: date.toISOString().split('T')[0] }
      });
      setTrialBalance(response.data || []);
    } catch (err) {
      setError('     .');
      console.error('Error fetching trial balance:', err);
    } finally {
      setLoading(false);
    }
  }, [date]);

  useEffect(() => {
    fetchTrialBalance();
  }, [fetchTrialBalance]);

  if (loading) {
    return (
      <Box display="flex" justifyContent="center" alignItems="center" minHeight="200px">
        <CircularProgress />
      </Box>
    );
  }

  if (error) {
    return (
      <Alert severity="error" sx={{ mt: 2 }}>
        {error}
      </Alert>
    );
  }

  const totalDebit = trialBalance.reduce((sum, item) => sum + (item.debitBalance || 0), 0);
  const totalCredit = trialBalance.reduce((sum, item) => sum + (item.creditBalance || 0), 0);

  return (
    <Box sx={{ p: 3 }}>
      <Box sx={{ mb: 3, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <Typography variant="h4"></Typography>
        <Box>
          <Tooltip title=" ">
            <IconButton onClick={fetchTrialBalance} sx={{ mr: 1 }}>
              <RefreshIcon />
            </IconButton>
          </Tooltip>
          <LocalizationProvider dateAdapter={AdapterDateFns}>
            <DatePicker
              label=""
              value={date}
              onChange={(newDate) => {
                setDate(newDate);
                fetchTrialBalance();
              }}
            />
          </LocalizationProvider>
        </Box>
      </Box>

      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell align="right"></TableCell>
              <TableCell align="right"></TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {trialBalance.length > 0 ? (
              <>
                {trialBalance.map((entry) => (
                  <TableRow key={entry.accountCode}>
                    <TableCell>{entry.accountCode}</TableCell>
                    <TableCell>{entry.accountName}</TableCell>
                    <TableCell align="right">{entry.debitBalance?.toLocaleString() || '-'}</TableCell>
                    <TableCell align="right">{entry.creditBalance?.toLocaleString() || '-'}</TableCell>
                  </TableRow>
                ))}
                <TableRow>
                  <TableCell colSpan={2} align="right">
                    <strong></strong>
                  </TableCell>
                  <TableCell align="right">
                    <strong>{totalDebit.toLocaleString()}</strong>
                  </TableCell>
                  <TableCell align="right">
                    <strong>{totalCredit.toLocaleString()}</strong>
                  </TableCell>
                </TableRow>
              </>
            ) : (
              <TableRow>
                <TableCell colSpan={4} align="center">
                   .
                </TableCell>
              </TableRow>
            )}
          </TableBody>
        </Table>
      </TableContainer>
    </Box>
  );
};

export default TrialBalance; 